.App {
  display: flex;
  flex-direction: row;
  position: absolute;
  top: 0; left: 0; right: 0; bottom: 0;
}

.main {
  display: flex;
  flex-direction: row;
  flex-grow: 1;
}

.scroll-list-container {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.scroll-list {
  overflow: auto;
  flex-grow: 1;
}

.right-section {
  display: flex;
  flex-grow: 1;
}
